.greetingForm {
    align-self: start;
    /*justify-items: start;*/
}

.inputAndButtonContainer {
    display: flex;
    gap: 15px;
}

.error {
    color: #C00;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.input {
    width: 370px;
    height: 35px;
    border-radius: 5px;
    border: 1px solid #D1D1D1;
}

.input:focus {
}

.errorInput {
    width: 370px;
    height: 35px;
    border-radius: 5px;
    border: 1px solid #C00;
}

.button {
    width: 78px;
    height: 35px;
    color: #fff;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 20px; /* 142.857% */

    display: inline-flex;
    padding: 5px 24px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    flex-shrink: 0;

    cursor: pointer;

    text-transform: capitalize;

    border-radius: 3px;
    border: none;
    background: #06C;
}

.button:focus {
}

.button:active {
}

.button:disabled {
    background: #06C;
    opacity: 0.5;
    cursor: auto;
}

.text {
    color: rgba(0, 0, 0, 0.50);
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    padding-bottom: 10px;
}

.greeting {
    text-transform: capitalize;
    color: #000;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}
