.App {
    --header_height: 60px;
    padding-bottom: 50px;
}

.hw {
    position: relative;
    margin: 0 80px;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    max-width: 1280px;
    width: 100%;
    min-height: 375px;
}

.hw::after {
    content: '';
    position: absolute;
    color: #D9D9D9;
    border: 1px solid #D9D9D9;
    width: 100%;
    max-width: 1280px;
    left: 0;
    bottom: 0;
}

.hwTitle {
    position: relative;
    padding: 40px 0 40px 80px;
    color: #000;
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    width: 100%;
    max-width: 1280px;
}

.hwTitle::after {
    content: '';
    position: absolute;
    color: #D9D9D9;
    border: 1px solid #D9D9D9;
    width: 100%;
    max-width: 1280px;
    left: 80px;
    bottom: 30px;
}

.hwMessages {
    width: 100%;
    display: flex;
    flex-direction: column;
}